import React from "react"
import {
  DocumentsCollectionAudioItem,
  DocumentsCollectionEventInfo,
} from "@rp/react/tanstack-api/documents"
import { useAudioPlayer } from "@rp/react/web/common/base-components"
import { Button } from "@rp/react/web/mui/core"
import { Pause, PlayArrow } from "@rp/react/web/mui/icons"
import { DocumentsCollectionAudioBar } from "../DocumentsCollectionAudioBar/DocumentsCollectionAudioBar"

type DocumentsCollectionAudioButtonProps = {
  audioItem: DocumentsCollectionAudioItem | null
  eventInfo: DocumentsCollectionEventInfo | null
}

const DocumentsCollectionAudioButton = ({
  audioItem,
  eventInfo,
}: DocumentsCollectionAudioButtonProps) => {
  const audioPlayer = useAudioPlayer({ url: audioItem?.data.url })
  if (!audioItem) return null

  const { isPlaying, play, pause } = audioPlayer

  return (
    <>
      <Button
        aria-label="Play call"
        sx={{ mt: 0.25 }}
        startIcon={isPlaying ? <Pause /> : <PlayArrow />}
        variant="text-outlined"
        onClick={isPlaying ? pause : play}
      >
        {isPlaying ? "Pause call" : "Play call"}
      </Button>

      <DocumentsCollectionAudioBar {...audioPlayer} audioItem={audioItem} eventInfo={eventInfo} />
    </>
  )
}

export { DocumentsCollectionAudioButton }
